/* Custom Font */
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;700&display=swap');
/* @import url('https://fonts.googleapis.com/icon?family=Material+Icons'); */

/* Initial reset */
*, *::before, *::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
ul {list-style-type: none}
a {text-decoration: none}

/* Variables */
:root {
  --dark-bg: #2c2c54;
  --light-bg: #d2dae2;
  --greyish-bg: #d1ccc0;
  --primary-font: #f7f1e3;
  --secondary-font: #ff5252;
  --button-green: #33d9b2;
}

body {
  font-family: 'Raleway', sans-serif;
  background-color: var(--light-bg);
}

.App {
  position: relative;
  height: 100vh;
}

.App-header {
  background-color: var(--dark-bg);
  padding: 0.88rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.App-header__title {
  color: var(--primary-font);
  text-transform: uppercase;
  font-size: 1.4rem;
}
.navbar {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--dark-bg);
  /* height: 100vh; */
  -webkit-transition: left 0.5s cubic-bezier(0.52, -0.12, 0.71, 1.24);
  -o-transition: left 0.5s cubic-bezier(0.52, -0.12, 0.71, 1.24);
  transition: left 0.5s cubic-bezier(0.52, -0.12, 0.71, 1.24);
}
.navbar .navbar-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.navbar .navbar-list .navbar-list__item {
  padding: 1rem;
}
.navbar .navbar-list .navbar-list__item a {
  color: var(--primary-font);
  text-transform: uppercase;
  font-size: .85rem;
  font-weight: 600;
}

/* Homepage, About, Contact, & Testimonials */
.homepage, .about, .contact, .testimonials {
  max-width: 1100px;
  background-color: #efefef;
  margin: 0 auto;
  padding: 1rem;
}
.homepage__jumbotron {
  height: 540px;
  width: 100%;
  background-image: url('https://images.unsplash.com/photo-1615529189232-5d380facaf73?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=80');
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.homepage .homepage__jumbotron .slogan {
  background: var(--dark-bg);
  padding: 1.5rem;
  color: #fff;
  position: absolute;
  top: 4rem;
  left: -1rem;
  max-width: 200px;
  font-size: 1.4rem;
  font-weight: 700;
}
.homepage__content {
  margin: 1rem auto;
  /* padding: 10px; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}
.homepage__content h2 {
  margin: 15px auto;
  color: var(--secondary-font);
  text-align: center;
}
.homepage__content article {
  margin: .25rem 0;
  padding: .75rem;
  background-color: var(--light-bg);
  border-radius: .45rem;
  /* text-align: center; */
  /* background-color: var(--dark-bg); */
}

#cta {
  margin: auto auto 5rem;
  padding: 4rem;
  background-color: var(--dark-bg);
  border-radius: .45rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  /* place-items: center; */
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
#cta h3 {
  color: var(--secondary-font);
  font-size: 2.8rem;
}
#cta form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
#cta form input[type="text"], textarea {
  margin-bottom: .5rem;
  padding: .5rem;
  border-top: none;
  border-right: none;
  border-left: none;
  /* background-color: #d1ccc0; */
  border-radius: .25rem;
}
#cta form input[type="text"]:focus, textarea:focus {
  outline: none;
  border-bottom: 2px solid var(--secondary-font);
}
#cta form label {
  font-size: .78rem;
  font-weight: 600;
  color: var(--secondary-font);
  margin-bottom: .65rem;
}
#cta button {
  margin: 1rem auto auto;
  padding: .5rem 1.5rem;
  background-color: var(--button-green);
  border: none;
  outline: none;
  color: var(--dark-bg);
  font-size: 1rem;
  font-weight: 700;
  border-radius: .45rem;
  cursor: pointer;
  width: 268px;
}

/* Responsive CTA and <form> */
@media only screen and (max-width: 540px) {
  #cta {
    display: grid;
    grid-template-columns: 1fr;
    padding: 2rem;
  }
  #cta h3 {
    text-align: center;
    font-size: 1.4rem;
  }
}

/* Responsive - Mobile First approach */
@media screen and (max-width: 580px) {
  .homepage__jumbotron {
    height: 320px;
  }
}
@media screen and (max-width: 320px) {
  .homepage__jumbotron {
    height: 300px;
  }
}

/* About, Contact, and Testimonials common styles */
.about h4, .contact h4, .testimonials h4 {
  color: var(--secondary-font);
  font-size: 2rem;
}

/* 'Contact' CTA <form> */
.contact {
  display: grid;
  grid-template-columns: 1fr 2fr;
}
/* Responsive Contact styles*/
@media only screen and (max-width: 540px) {
  .contact {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
}

.contact .cta-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-top: 1.5rem;
  padding: 2rem;
}
.contact .cta-form input[type="text"], .contact .cta-form textarea {
  margin-bottom: .5rem;
  padding: .5rem;
  border-top: none;
  border-right: none;
  border-left: none;
  /* background-color: #d1ccc0; */
  border-radius: .25rem;
}
.contact .cta-form input[type="text"]:focus, .contact .cta-form textarea:focus {
  outline: none;
  border-bottom: 2px solid var(--secondary-font);
}
.contact .cta-form button {
  margin: 1rem auto auto;
  padding: .5rem 1.5rem;
  background-color: var(--secondary-font);
  border: none;
  outline: none;
  color: var(--primary-font);
  font-size: 1rem;
  font-weight: 700;
  border-radius: .45rem;
  cursor: pointer;
  width: 80%;
}

/* 'Testimonial' single/individual template styles */
.testimonial {
  background-color: var(--light-bg);
  border-radius: .45rem;
  margin-bottom: .75rem;
  padding: 1rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  -ms-flex-align: center;
      align-items: center;
}
/* Responsive styles for 'Testimonial' */
@media only screen and (max-width: 540px) {
  .testimonial, .testimonial .testimonial-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }
  .testimonial .testimonial-content h2 {margin-top: 1rem}
  .testimonial .testimonial-content p {text-align: center;}
}
.testimonial .testimonial-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.testimonial .testimonial-content h2 {
  color: var(--dark-bg);
  font-size: .98rem;
  margin-bottom: .5rem;
}
.testimonial .testimonial-content a {
  font-size: .88rem;
  font-weight: 500;
  margin-bottom: .5rem;
}
.testimonial .testimonial-content p {
  font-size: .98rem;
}
.testimonial .avatar {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  background-image: url('https://randomuser.me/api/portraits/women/82.jpg');
  background-position: center top;
  background-size: cover;
}



